<template>
  <v-layout class="hello" align-center column>
    <v-flex xs12 >
      <h2 class="App-intro">Here you can find all of your most loved series.</h2>
      <v-text-field label="Series Name" append-icon="search" autofocus v-model='seriesName' v-on:input="onSeriesInputChange" ></v-text-field>
      <center><h4 v-if="series.length===0 && seriesName.trim()===''">Please enter Series Name into the input.</h4>
      <h4 v-else-if="!isFetching && series.length===0 && seriesName.trim()!==''">Oops!! No TV Series have been found by this name.</h4>
      <v-progress-circular v-else-if="isFetching" indeterminate></v-progress-circular>
      <SeriesList v-else-if="!isFetching && series.length!==0" :list="series" /></center>
    </v-flex>
  </v-layout>
</template>

<script>
import SeriesList from './SeriesList.vue'
export default {
  data () {
    return {
      series: [],
      seriesName: '',
      isFetching: false
    }
  },
  methods: {
    onSeriesInputChange (e) {
      this.isFetching = true
      fetch(`https://api.tvmaze.com/search/shows?q=${this.seriesName}`)
        .then(response => response.json())
        .then(json => {
          this.series = json
          this.isFetching = false
        })
    }
  },
  components: {
    SeriesList
  }
}
</script>

